import { createRoot } from 'react-dom/client';
import { PageLoader } from './ui/PageLoader';

export function initPageLoader(): void {
  const pageLoaderRoot = document.createElement('div');
  pageLoaderRoot.id = 'page-loader-root';

  document.addEventListener('DOMContentLoaded', () => {
    document.body.appendChild(pageLoaderRoot);

    createRoot(pageLoaderRoot).render(<PageLoader />);
  });
}
