import {
  LocalStorageKey,
  getLocalStorageData,
} from '@app/storage/local/shared';
import {
  mountCaptchaBanner,
  unmountCaptchaBanner,
} from '../components/CaptchaBanner';

function isCaptchaPage(url: string): boolean {
  return (
    url.includes('google.com/sorry') &&
    document.title.includes('youtube.com/watch')
  );
}

async function checkAndUpdateBanner(): Promise<void> {
  const { targetState } = await getLocalStorageData(
    LocalStorageKey.TargetState,
  );

  if (isCaptchaPage(window.location.href) && targetState === 'disconnected') {
    mountCaptchaBanner();
  } else {
    unmountCaptchaBanner();
  }
}

export function initCaptchaBanner(): void {
  void checkAndUpdateBanner();

  // Listen for storage changes
  chrome.storage.onChanged.addListener((changes) => {
    if (changes[LocalStorageKey.TargetState]) {
      void checkAndUpdateBanner();
    }
  });

  // Listen for URL changes
  let lastUrl = window.location.href;
  new MutationObserver(() => {
    if (window.location.href !== lastUrl) {
      lastUrl = window.location.href;
      void checkAndUpdateBanner();
    }
  }).observe(document, { subtree: true, childList: true });
}
