import { Root, createRoot } from 'react-dom/client';
import { CaptchaBanner } from './ui/CaptchaBanner';

const BANNER_ID = 'vpn-captcha-banner';
let root: Root | null = null;

export function mountCaptchaBanner(): void {
  if (document.getElementById(BANNER_ID)) return;

  const container = document.createElement('div');
  container.id = BANNER_ID;
  document.body.appendChild(container);

  root = createRoot(container);
  root.render(<CaptchaBanner />);
}

export function unmountCaptchaBanner(): void {
  const container = document.getElementById(BANNER_ID);
  if (container && root) {
    root.unmount();
    container.remove();
    root = null;
  }
}
