import { Actions } from '../../app/actions/shared';
import {
  type VideoOpenerReadyMessage,
  type Message,
} from '../../app/messages/shared';
import { initVideoLoader } from '../components/VideoLoader/VideoLoader';
import {
  LocalStoragePremiumKey,
  getLocalStoragePremiumData,
} from '../../app/storage/local/premium';
import { Events, type ShowPageLoaderEvent } from '../../app/events';

export async function initVideoOpener(): Promise<void> {
  chrome.runtime.onMessage.addListener((message: Message) => {
    if (message.action === Actions.ShowPageLoader) {
      document.dispatchEvent(
        new CustomEvent<ShowPageLoaderEvent>(Events.ShowPageLoader, {
          detail: {
            isShown: true,
            loaderType: 'page',
          },
        }),
      );
    }
  });

  chrome.runtime.onMessage.addListener(async (message: Message) => {
    try {
      if (message.action === Actions.DirectVideoOpen) {
        const { hasPremium, status } = await getLocalStoragePremiumData([
          LocalStoragePremiumKey.HasPremium,
          LocalStoragePremiumKey.Status,
        ]);

        if (status === 'Free') {
          document.dispatchEvent(
            new CustomEvent<ShowPageLoaderEvent>(Events.ShowPageLoader, {
              detail: {
                isShown: true,
                loaderType: 'video',
                videoPayload: message.payload,
              },
            }),
          );
        } else {
          document.dispatchEvent(
            new CustomEvent<ShowPageLoaderEvent>(Events.ShowPageLoader, {
              detail: {
                isShown: true,
                loaderType: 'page',
              },
            }),
          );
          initVideoLoader(message.payload);
        }
      }
    } catch (error) {
      console.error('error', error);
    }
  });

  chrome.runtime.onMessage.addListener((message: Message) => {
    try {
      if (message.action === Actions.FailedVideoOpen) {
        document.dispatchEvent(
          new CustomEvent<ShowPageLoaderEvent>(Events.ShowPageLoader, {
            detail: { isShown: false },
          }),
        );

        alert(
          `${process.env.EXTENSION_TITLE} не смог открыть видео ${message.payload.watchUrl} :(`,
        );
      }
    } catch (error) {
      console.error('error', error);
    }
  });

  await chrome.runtime.sendMessage<VideoOpenerReadyMessage>({
    action: Actions.VideoOpenerReady,
  });
}
