import { Actions } from '@app/actions/shared';
import { getVideoId } from '@background/redirect/utils';

/**
 * Initializes the time saver
 * @description This function listens for the beforeunload event and saves the current time of the video
 */
export function initTimeSaver(): void {
  window.addEventListener('beforeunload', () => {
    const video = document.querySelector('video');

    const videoId = getVideoId(window.location.href);

    if (video && videoId) {
      const time = video.currentTime;

      chrome.runtime.sendMessage({
        action: Actions.SaveTime,
        payload: { time, videoId },
      });
    }
  });

  console.warn('Time saver initialized');
}
