import { useCallback, useEffect, useMemo, useState } from 'react';
import {
  type ConnectionState,
  LocalStorageKey,
  getLocalStorageData,
} from '../../../../app/storage/local/shared';
import {
  Events,
  type LoaderType,
  type ShowPageLoaderEvent,
} from '../../../../app/events';
import { type DirectVideoOpenPayload } from '../../../../app/messages/shared';
import { applyVideoNavigation } from '../../VideoLoader/VideoLoader';
import {
  LocalStoragePremiumKey,
  type PaymentStatus,
  getLocalStoragePremiumData,
} from '../../../../app/storage/local/premium';

type PageLoaderHookValues = {
  isShown: boolean;
  loaderType: LoaderType;
  isPremiumUser: boolean;
  handleTimerComplete: () => void;
};

export function usePageLoader(): PageLoaderHookValues {
  const [isPremiumUser, setIsPremiumUser] = useState(false);
  const [isShown, setIsShown] = useState(false);
  const [loaderType, setLoaderType] = useState<LoaderType>('page');
  const [videoPayload, setVideoPayload] =
    useState<DirectVideoOpenPayload | null>(null);

  const handleTimerComplete = useCallback(() => {
    if (videoPayload) {
      applyVideoNavigation(videoPayload);
    }
  }, [videoPayload]);

  useEffect(() => {
    async function getPremiumInfo(): Promise<void> {
      const { hasPremium, status } = await getLocalStoragePremiumData([
        LocalStoragePremiumKey.HasPremium,
        LocalStoragePremiumKey.Status,
      ]);

      setIsPremiumUser(Boolean(hasPremium && status === 'Paid'));
    }
    void getPremiumInfo();
  }, []);

  useEffect(() => {
    async function listener(changes: {
      hasPremium?: { newValue?: boolean };
      status?: { newValue?: PaymentStatus };
    }): Promise<void> {
      if (changes.status) {
        const { hasPremium } = await getLocalStoragePremiumData(
          LocalStoragePremiumKey.HasPremium,
        );
        setIsPremiumUser(
          Boolean(hasPremium && changes.status.newValue === 'Paid'),
        );
      }

      if (changes.hasPremium) {
        const { status } = await getLocalStoragePremiumData(
          LocalStoragePremiumKey.Status,
        );
        setIsPremiumUser(
          Boolean(changes.hasPremium.newValue && status === 'Paid'),
        );
      }
    }
    chrome.storage.onChanged.addListener(listener);
    return () => {
      chrome.storage.onChanged.removeListener(listener);
    };
  }, []);

  useEffect(() => {
    async function handleVideoLoader(): Promise<void> {
      const { currentState } = await getLocalStorageData([
        LocalStorageKey.CurrentState,
      ]);

      if (currentState === 'disconnected') {
        setIsShown(false);
      }
    }
    void handleVideoLoader();
  }, []);

  useEffect(() => {
    function listener(changes: {
      currentState?: { newValue: ConnectionState };
    }): void {
      if (changes.currentState?.newValue === 'disconnected') {
        setIsShown(false);
      }
    }
    chrome.storage.local.onChanged.addListener(listener);
    return () => {
      chrome.storage.local.onChanged.removeListener(listener);
    };
  }, []);

  useEffect(() => {
    function listener(e: Event): void {
      const event = e as CustomEvent<ShowPageLoaderEvent>;
      setIsShown(event.detail.isShown);
      setLoaderType(event.detail.loaderType ?? 'page');
      setVideoPayload(event.detail.videoPayload ?? null);
    }
    document.addEventListener(Events.ShowPageLoader, listener);
    return () => {
      document.removeEventListener(Events.ShowPageLoader, listener);
    };
  }, []);

  return useMemo(
    () => ({ isShown, loaderType, handleTimerComplete, isPremiumUser }),
    [handleTimerComplete, isPremiumUser, isShown, loaderType],
  );
}
