import { Actions } from '../../../app/actions/shared';
import { Events, type ShowPageLoaderEvent } from '../../../app/events';
import {
  type DirectVideoOpenPayload,
  type FailedVideoOpenMessage,
} from '../../../app/messages/shared';

export function initVideoLoader(payload: DirectVideoOpenPayload): void {
  const applyVideoNavigationDelay = process.env.APPLY_VIDEO_NAVIGATION_DELAY
    ? parseInt(process.env.APPLY_VIDEO_NAVIGATION_DELAY, 10)
    : 1000;
  document.addEventListener(Events.YtIsReady, function listener() {
    setTimeout(() => {
      applyVideoNavigation(payload);
    }, applyVideoNavigationDelay);

    document.removeEventListener(Events.YtIsReady, listener);
  });
}

function addYtNavigateFinishEventListener(watchUrl: string): void {
  document.addEventListener(Events.YtNavigateFinish, function listener() {
    if (location.href.includes(watchUrl)) {
      document.dispatchEvent(
        new CustomEvent<ShowPageLoaderEvent>(Events.ShowPageLoader, {
          detail: { isShown: false },
        }),
      );
      document.removeEventListener(Events.YtNavigateFinish, listener);
    }
  });
}

export function applyVideoNavigation({
  time,
  videoId,
  watchUrl,
}: DirectVideoOpenPayload): void {
  dispatchCustomYtNavigateEvent({
    time: time ? parseInt(time, 10) : null,
    videoId,
    watchUrl,
  });
  addYtNavigateFinishEventListener(watchUrl);
}

function getUrlWithParams(
  watchUrl: string,
  time?: number | null,
  list?: string | null,
  index?: string | null,
): string {
  const urlObj = new URL(watchUrl, window.location.origin);
  if (list) {
    urlObj.searchParams.set('list', list);
  }

  if (index) {
    urlObj.searchParams.set('index', index);
  }

  if (time) {
    urlObj.searchParams.set('t', `${time}s`);
  }

  return urlObj.pathname + urlObj.search;
}

function dispatchCustomYtNavigateEvent({
  time,
  videoId,
  watchUrl,
}: {
  time: number | null;
  videoId: string;
  watchUrl: string;
}): void {
  const urlObj = new URL(watchUrl, window.location.origin);
  const list = urlObj.searchParams.get('list');
  const index = urlObj.searchParams.get('index');

  const navigateEvent = new CustomEvent(Events.YtNavigate, {
    detail: {
      endpoint: {
        commandMetadata: {
          webCommandMetadata: {
            rootVe: 3832,
            url: getUrlWithParams(watchUrl, time, list, index),
            webPageType: 'WEB_PAGE_TYPE_WATCH',
          },
        },
        watchEndpoint: {
          startTimeSeconds: time ?? 0,
          videoId,
          ...(list && { playlistId: list }),
          ...(index && { index: parseInt(index, 10) }),
        },
      },
    },
  });

  const app = document.querySelector('ytd-app');

  if (app) {
    app.dispatchEvent(navigateEvent);
  } else {
    void chrome.runtime.sendMessage<FailedVideoOpenMessage>({
      action: Actions.FailedVideoOpen,
      payload: { watchUrl: `https://www.youtube.com${watchUrl}` },
    });
  }
}
